/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-boat-configurator {
	border: 1px dotted rgb(0, 195, 255);
	border-radius: .8em;
	padding: 1em;
	background-color: white;
}

/* HIDE RADIO */
[type=radio] {
	position: absolute;
	opacity: 0;
	width: 0;
	height: 0;
}

/* IMAGE STYLES */
[type=radio]+img {
	cursor: pointer;
}

/* CHECKED STYLES */
[type=radio]:checked+.card {
	outline: 2px solid #f00;
}

.container {
	padding: 0px;
	display: flex;
	align-items: start;
	justify-content: flex-start;
	flex-wrap: wrap;
}

.card {
	flex: 1 1 auto;
	height: 320px;
	max-width: 350px;
	width: calc(33% - 20px);
	min-width: 250px;
	margin-right: 20px;
	background: white;
	transition: 0.4s;
	box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
	border-radius: 0.8rem;
}

.card-plus {
    max-height: max-content;
    max-width: -moz-max-content;
    min-width: max-content;
    margin-top: 4%;
}

.card .img {
	height: 50%;
	max-width: 100%;

}

.card .img img {
	height: 100%;
	width: 100%;
	object-fit: cover;
	border-radius: 0.8rem;
	// max-height: 200px;
	// max-width: 200px;
}

.card .top-text {
	padding: 5px 0;
    max-width: 100%;
}

.card .top-text .option {
	font-size: 20px;
	color: #202020;
}

span.add-option {
	font-size: 60px;
	color: #8080808c;
	cursor: pointer;
}

span.delete-option {
	padding-left: 100%;
	cursor: pointer;
	color: #9d3a3a;
}

div.delete-question {
	display: flex;
    //justify-content: center;
	align-items: flex-start!important;
    height: 100%;
}

.question-container {
	margin-bottom: 2em;
}

.delete-btn {
	cursor: pointer;
	//margin-top: 50%;
	color: #a73030;
	font-size: 40px;
	margin-top: -.8em;
	// padding-top: 40%;
}

.accordion {
	border: 1px solid #b0afaf;
	background-color: #f9f9f9;
	margin-bottom: 20px;
	border-radius: .8em;
}

.accordion-header {
	padding: 10px;
	background-color: #f9f9f9;
	color: #030303;
	border-top-left-radius: .8em;
	border-top-right-radius: .8em;
}

.accordion-header.closed {
	border-radius: .8em;
}

.accordion-content {
	padding: 10px;
	background-color: white;
	margin-top: .5em;
	border-radius: .8em;
}

.question-header {
	align-content: flex-start !important;
	justify-content: flex-start !important;
	flex: 18;
}



.question-header .components-flex-item,
.question-arrows .components-flex-item,
.question-expand .components-flex-item,
{
	cursor: pointer !important;
}


.question-header .components-flex-item,
{
	display: flex;
    column-gap: .4em;
}


.stacked.components-flex-block {
	flex-direction: column;
    display: flex;
    max-width: max-content;
    border-right: inset .15em;
    padding-right: .4em;
}

.question-expand {
	align-content: flex-end !important;
	justify-content: flex-end !important;
	flex: 1;
}

.input-bc-custom {
	position: relative;


&__label {
		position: absolute;
		left: 1em;
		top: -.8em;
		padding: 0 .5em;
		font-size: small;
		/* padding: calc(var(--size-bezel)* 0.75) calc(var(--size-bezel)* 0.5); */
		/* margin: calc(var(--size-bezel)* 0.75 + 3px) calc(var(--size-bezel)* 0.5); */
		background: white;
		white-space: nowrap;
		color: #b0afaf;
		/* font-weight: bold; */
		/* line-height: 1.2; */
	}
	&__label.question {
		background: white;
	}
}

	input[type=text].input-bc-custom__field {
		box-sizing: border-box;
		display: block;
		width: 100%;
		border: 1px solid #b0afaf;
		padding: 0.8em 1.4em;
		color: black;
		background: transparent;
		border-radius: 0.6em;

		&:focus,
		&:not(:placeholder-shown) {
			&+.input__label {
				color: black;
			}
		}
	}


@media screen and (max-width: 1000px) {
	.card {
		width: calc(50% - 20px);
        max-width: 50%;
	}
}
  
@media screen and (max-width: 676px) {
  
	.card {
		width: calc(100% - 20px);
        max-width: 100%;
		margin-right: 0;
	}
  }